.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
#backgroundImg{
  height:20rem;
  width: 20rem;
  background-color: #10D876;
  display: block;
  border-radius: 20px 0px 0px 0;
  top:0px;
  left: 0px;
  transform-origin: top right;
  transform: skew(-30deg, 0deg);

} 
html,
body,
#app {
  height: 100%;
}

#app {
  width: 100%;
  display: flex;
  justify-content: center;
}

body {
  & * {
    box-sizing: border-box;
  }
}

.slideContent {
  width: 250px;
  height: 300px;
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  transition: transform 0.5s ease-in-out;
  opacity: 0.7;
  display: grid;
  align-content: center;
  transform-style: preserve-3d;
  transform: perspective(1000px) translateX(calc(100% * var(--offset)))
    rotateY(calc(-45deg * var(--dir)));
  border-radius: 15px;
}

.slideContentInner {
  transform-style: preserve-3d;
  transform: translateZ(2rem);
  transition: opacity 0.3s linear;
  color: white;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.6);
  opacity: 0;

  /* slideSubtitle,
 

  .slideSubtitle::before {
    content: "— ";
  } */

  /* .slideDescription {
    margin: 0;
    font-size: .8rem;
    letter-spacing: 0.2ch;0
  } */
}
.slideContentInner  > slideSubtitle::before {
  content: "— ";
}
.slideContentInner > slideTitle {
  font-size: 2rem;
  font-weight: normal;
  letter-spacing: 0.2ch;
  text-transform: uppercase;
  margin: 0;
}
.slideContentInner  > slideDescription {
  margin: 0;
  font-size: .8rem;
  letter-spacing: 0.2ch;
}

.slideBackground {
  position: fixed !important;
  top: 0;
  left: -10%;
  right: -10%;
  bottom: 0;
  background-size: cover;
  background-position: center center;
  z-index: -1;
  opacity: 0;
  transition: opacity 0.3s linear, transform 0.3s ease-in-out;
  pointer-events: none;
  transform: translateX(calc(10% * var(--dir)));
}

.slide[data-active] {
  z-index: 2;
  pointer-events: auto;

  .slideBackground {
    opacity: 0.1;
    transform: none;
  }

  .slideContentInner {
    opacity: 1;
  }

  .slideContent {
    --x: calc(var(--px) - 0.5);
    --y: calc(var(--py) - 0.5);
    opacity: 1;
    transform-style: preserve-3d;
    transform: perspective(1000px) translateX(calc(100% * var(--offset)));
    transition: transform 0.5s ease-in-out;

    &:hover {
      transition: none;
      transform: perspective(1000px) rotateY(calc(var(--x) * 45deg))
        rotateX(calc(var(--y) * -45deg));
    }
  }
}

.slidesWrapper {
  min-height: 500px;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  width: 100%;

}

.slides {
  display: grid;
}
.slides > .prevSlideBtn, .nextSlideBtn{
  appearance: none;
    background: transparent;
    border: none;
    color: white;
    text-shadow: 0 1px 2px rgba(0, 0, 0, 0.6);
    position: absolute;
    font-size: 2rem;
    width: 5rem;
    height: 5rem;
    top: 30%;
    transition: opacity 0.3s;
    opacity: 0.7;
    z-index: 5;
}

.slides > .nextSlideBtn{
  right: -25%;
}
.slides > .slide {
  grid-area: 1 / -1;
}

@media (min-width: 768px) {
  .slidesWrapper{
    width: 750px;
  }
}

@media (min-width: 992px) {
  .slidesWrapper{
    width: 960px;
  }
}

@media (min-width: 1220px) {
  .slidesWrapper{
    width: 1200px;
  }
}